import React from "react"
import { Link } from "gatsby"
import { useTranslation } from 'react-i18next'
import "../styles/header.css"
import Logo from "../../assets/en_logo.svg"
import AboutIcon from "../../assets/menu_icon_about.svg"
import BibleIcon from "../../assets/menu_icon_bibles.svg"
import LanguageIcon from "../../assets/menu_icon_languages.svg"
import CountryIcon from "../../assets/menu_icon_countries.svg"
import AgencyIcon from "../../assets/menu_icon_agencies.svg"

function Header() {
  const { t } = useTranslation()
  return (
      <header id="page-header">
        <Link to="/"><Logo style={{width:"70px"}} /></Link>
        <Link className="small-hide" to="/about/"><AboutIcon className="icon" />{ t('translation:about.title') }</Link>
        <Link to="/members/"><AgencyIcon className="icon" />{ t('translation:organizations.title') }</Link>
        <Link className="small-hide" to="/countries/"><CountryIcon className="icon" />{ t('translation:countries.title') }</Link>
        <Link to="/languages"><LanguageIcon className="icon" />{ t('translation:languages.title') }</Link>
        <Link to="/bibles/"><BibleIcon className="icon" />{ t('translation:bibles.title') }</Link>
      </header>
  )
}

export default Header