import React from 'react'
import _ from 'lodash'
import Layout from '../components/layout'
import Banner from '../components/banner'
import Helpers from '../components/helpers'
import SEO from '../components/seo'

import { useTranslation } from 'react-i18next'
import '../styles/bible-single.css'
import BibleIcon from '../../assets/menu_icon_bibles.svg';
import {Link} from 'gatsby';

// Basic bible component
export default ({ pageContext: { bible } }) => {
  const { t, i18n } = useTranslation();

  // Lazy Load Fonts if the Lords of Tofu require it
  // if(bible.alphabet.requires_font) {
  //   Helpers.loadFont(bible.alphabet.script.toLowerCase())
  // }

  bible.names = Helpers.names(bible.translations)

  // Generate Link HTML
  let bibleHtml = ''
  _.forEach(_.groupBy(bible.links, 'type'), function(resources, type) {
    bibleHtml += '<div class="row">' +
        '<div class="type type-' + type + '">' +
        '<h2 class="link-type-title">' + t('bible.action.' + type.toLowerCase()) + ' ' + t('fields.in') + ' ' + bible.language.name  + '</h2>' +
        '<div class="link-type-description">' + t('bible.action.' + type.toLowerCase() + '_description') + '</div>';
    _.forEach(_.groupBy(resources, 'provider'), function(links, provider) {
      bibleHtml += '<div class="panel shadow">' +
          '<div class="small-4 large-3 columns organization-section">' +
          // '<object class="provider-img" data="https://bible.cloud/images/logo/' + provider.replace(' ','-').toLowerCase() + '.svg" type="image/svg+xml">' + provider + '</object>' +
          provider + '</div>' +
          '<div class="medium-8 large-9 columns link-section">'
      _.forEach(links, function(link) {
        if(link.url) bibleHtml += '<a class="link color-' + type + '" target="_blank" rel="noopener noreferrer" itemProp="url" href="' + link.url + '">' + link.title +'</a>';
      })
      bibleHtml += '</div></div>'
    })
    bibleHtml += "</div></div>"
  })

  return (
    <Layout>
      <SEO title={bible.names.name + ' ' + bible.names.vname} description={ bible.names.description } lang={ i18n.language } />
      <Banner
          title={ (bible.names.vname) ? bible.names.vname : bible.names.name }
          titleClass={bible.alphabet.script.toLowerCase()}
          subtitle={ (!bible.names.vname) ? '' : bible.names.name }
          image={ `https://images.bible.cloud/fab/banners/language/${ bible.language.iso.toUpperCase() }.jpg` }
          icon={BibleIcon}
          bannerClasses={ bible.alphabet.script.toLowerCase() }
          breadcrumbs={[
            {path: '/', title: 'home'},
            {path: '/bibles/', title: 'bibles'},
            {path: '#', title: bible.names.name}
          ]} />

      <aside id="bible-sidebar" className="medium-3 columns">
        <div className="row">
          <div><b>{ t('fields.alphabet') }</b>: <b>{ bible.alphabet.name }</b></div>
          <div><b>{ t('fields.copyright') }: </b>{ bible.mark}</div>
          <div><b>{ t('fields.date') }: </b>{ bible.date }</div>
          <div><b>{ t('fields.language') }: </b><Link to={"/langauges/" + bible.language.iso }/>{ bible.language.name } [{ bible.language.iso }]</div>
        </div>
        { bible.names.description ? <div><b>{ t('fields.description') }:</b> { bible.names.description } </div> : '' }
        { bible.names.vdescription ? <div><b>{ t('fields.vDescription') }:</b> { bible.names.vdescription } </div> : '' }
      </aside>

      <section className="medium-9 columns" dangerouslySetInnerHTML={{__html: bibleHtml }}></section>

    </Layout>
  )
}