import i18n from 'i18next';
import LanguageDetector from 'i18next-browser-languagedetector'
import { initReactI18next } from 'react-i18next'

import ar from "../../static/locales/ar/translation"
import ar_about from "../../static/locales/ar/about"
import en from "../../static/locales/en/translation"
import en_about from "../../static/locales/en/about"
import es from "../../static/locales/es/translation"
import es_about from "../../static/locales/es/about"
import ne from "../../static/locales/ne/translation"
import ne_about from "../../static/locales/ne/about"

i18n
  .use(LanguageDetector)
  .use(initReactI18next)
  // for all options read: https://www.i18next.com/overview/configuration-options
  .init({
    fallbackLng: 'en',
    preload: ['ar','en', 'es', 'ne'],
    resources: {
      ar: { translation: ar, about: ar_about },
      en: { translation: en, about: en_about },
      es: { translation: es, about: es_about },
      ne: { translation: ne, about: ne_about },
    },
    ns: ['translation', 'about'],
    load: 'languageOnly',
    debug: true,

    interpolation: {
      escapeValue: false, // not needed for react as it escapes by default
    },

    react: {
      wait: true,
    },

  });


export default i18n;