import _ from 'lodash';

export default {
  names: function(translations){
    let names = {}
    let name = _.find(translations, function(translation) { return translation.language_id === 6414 })
    let vname = _.find(translations, function(translation) { return translation.vernacular === 1 })
    names.name = (name) ? name.name : ''
    names.description = (name) ? name.description : ''
    names.vname = (vname) ? vname.name : ''
    names.vdescription = (name) ? name.description : ''

    if(names.name === names.vname) {
      names.vname = ''
    }

    if(names.description === names.vdescription) {
      names.vdescription = ''
    }
    return names
  },
  loadFont: function(font){
    let font_file = new FontFace(font, 'url(/fonts/'+ font +'.ttf)');
    font_file.load().then(function(loaded_font) {
      document.fonts.add(loaded_font);
      let all_script_elements = document.getElementsByClassName(font);
      for (let i = 0; i < all_script_elements.length; i++) {
        all_script_elements[i].style.fontFamily = font + ', Arial';
      }
    })
  }
}